import { Attack } from "../models/Action";
import React from "react";

interface Props{
    result: Attack;
}
export function ResultView(props: Props) {
    let attribute = null;
    if(props.result.attributeDamageResult !== null) {
        attribute = <React.Fragment>
                <p>{props.result.attributeDamageResult.knockedIntoWeak ? "" : ""} 
                 {props.result.attributeDamageResult.dpDamage > 0 ? `${props.result.attributeDamageResult.dpDamage}DP` : "" } </p>
                 {props.result.attributeDamageResult.damageMultipiler === null ? null : <p>: {props.result.attributeDamageResult.damageMultipiler.multiplierAdded}</p>}
            </React.Fragment>;
    }
    let dpRcovery = null;
    if(props.result.dpRecoveryResult !== 0) {
        dpRcovery = <p>DP{props.result.dpRecoveryResult} </p>
    }
    let spRecovery = null;
    if(props.result.spRecoveryResult !== 0) {
        spRecovery = <p>SP{props.result.spRecoveryResult} </p>
    }
    let defenceIncreasement = null;
    if(props.result.defenceIncreaseResult !== 0) {
        defenceIncreasement = <p>{props.result.defenceIncreaseResult}</p>
    }
    return <div className="result-view">
        <p>{props.result.action.user.name}  {props.result.target.name}  {props.result.action.data.name}</p>
        <p>{props.result.hpDamageResult.defended}</p>
        <p> {props.result.hpDamageResult.hpDamage}  {props.result.hpDamageResult.isDead ? "" : ""}</p>
        {attribute}
        {dpRcovery}
        {spRecovery}
        {defenceIncreasement}
    </div>
}